.sr-only {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  margin: -1px !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
  border: 0 !important;
}

@font-face {
  font-family: "gulfs_displaynormal";
  src: url("../../fonts/gulfsdisplay-normal-webfont.woff2") format("woff2"), url("../../fonts/gulfsdisplay-normal-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
body {
  font-family: "gulfs_displaynormal", sans-serif;
  font-size: 16px;
  line-height: 1.4;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
.h1,
h2,
.h2,
h3,
.h3 h4,
.h4 {
  font-family: "gulfs_displaynormal", sans-serif;
  font-weight: 500;
  text-align: center;
  color: #122E63;
}

h1 {
  font-size: 45px;
}

h2 {
  font-size: 40px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
h2:before {
  position: absolute;
  content: "";
  width: 300px;
  height: 200px;
  background-image: url("../img/logo_shade.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  z-index: -1;
}

h3 {
  margin: 0;
}

h4 {
  font-size: 14pt;
  margin: 0;
  line-height: 15pt;
}

p.tag {
  margin: 0;
  line-height: 14pt;
}
p.sous_texte {
  text-align: center;
  max-width: 70%;
  margin: auto;
}

footer {
  background-color: #122E63;
  z-index: 2;
  position: relative;
  color: #FFFFFF;
  text-align: center;
  margin: auto;
  font-size: 12px;
}
footer a {
  color: #FFFFFF;
}

.header {
  z-index: 10;
  position: fixed;
  background-color: #FFFFFF;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px;
  height: 68px;
}
.header--menu-is-open nav .menu-toggle {
  color: #FFFFFF;
}
.header--menu-is-open nav .menu-toggle span::before,
.header--menu-is-open nav .menu-toggle span::after {
  background-color: #FFFFFF;
}
.header--menu-is-open nav .menu-menu-container {
  right: 0;
}
.header--menu-is-open nav .menu-menu-container ul li {
  margin-bottom: 20px;
}
.header--menu-is-open nav .menu-menu-container ul li a {
  color: #FFFFFF;
}
.header .container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 0px;
  z-index: 10;
  position: relative;
}
.header .container img {
  max-width: 100%;
  padding: 0px 15px;
}
@media screen and (min-width: 60rem) {
  .header .container {
    padding: 0 5%;
    justify-content: space-between;
  }
}
.header .container .main-navigation {
  display: flex;
  justify-content: end;
}

nav {
  display: flex;
  flex-direction: row-reverse;
}
nav .menu-menu-container {
  position: absolute;
  background: #122E63;
  z-index: 2;
  width: 100%;
  right: -100vw;
  top: 0px;
  padding: 40px 0% 10px;
  transition: all 0.2s ease-in-out;
  list-style: none;
  margin: 0;
  color: #FFFFFF;
  border-bottom-left-radius: 30px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px;
}
@media screen and (min-width: 60rem) {
  nav .menu-menu-container {
    color: #122E63;
    position: relative;
    right: 0;
    box-shadow: none;
    border-bottom-left-radius: 0px;
    background-color: transparent;
    padding: 0;
    width: fit-content;
  }
}
nav .menu-menu-container::after {
  content: "";
  display: block;
  width: 100%;
  height: 40px;
  background-image: url("../../dist/img/logo_full_white.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
}
@media screen and (min-width: 60rem) {
  nav .menu-menu-container::after {
    display: none;
  }
}
nav .menu-menu-container ul {
  list-style-type: none;
  padding: 0;
  margin-top: 0;
  margin-bottom: 30px;
}
@media screen and (min-width: 60rem) {
  nav .menu-menu-container ul {
    margin-bottom: 0px;
    display: flex;
    justify-content: space-evenly;
    width: fit-content;
    align-items: center;
  }
}
nav .menu-menu-container ul li {
  font-size: 15pt;
  font-weight: 500;
  margin: 0px 20px;
  transition: all 0.2s ease-in-out;
  text-align: left;
}
@media screen and (min-width: 60rem) {
  nav .menu-menu-container ul li {
    text-align: center;
  }
}
nav .menu-menu-container ul li a {
  text-decoration: none;
  color: #122E63;
}

.menu-toggle {
  display: flex;
  align-items: center;
  background-color: transparent;
  border: none;
  line-height: 1;
  z-index: 15;
  text-decoration: none;
  color: #122E63;
}
@media screen and (min-width: 60rem) {
  .menu-toggle {
    display: none;
  }
}
.menu-toggle:hover {
  cursor: pointer;
}
.menu-toggle span,
.menu-toggle span::before,
.menu-toggle span::after {
  width: 1.125rem;
  border-radius: 0.125rem;
  background-color: #122E63;
  transition: 0.5s cubic-bezier(0.19, 1, 0.22, 1);
}
.menu-toggle span {
  display: block;
  position: relative;
  height: 0.125rem;
  margin-left: 0.2rem;
}
.menu-toggle span::before, .menu-toggle span::after {
  content: "";
  position: absolute;
  display: block;
  height: 100%;
  top: 0;
  left: 0;
}
.menu-toggle span::before {
  transform: translateY(-0.3125rem);
}
.header--menu-is-open .menu-toggle span::before {
  transform: rotate(-135deg);
}
.menu-toggle span::after {
  transform: translateY(0.3125rem);
}
.header--menu-is-open .menu-toggle span::after {
  transform: rotate(135deg);
}
.header--menu-is-open .menu-toggle span {
  background-color: transparent;
}

.slick-dots {
  display: flex;
  list-style-type: none;
  padding: 0;
  justify-content: center;
  margin: 5px auto;
}
.slick-dots li {
  position: relative;
  margin: 5px 7.5px;
  padding: 0;
  cursor: pointer;
}
.slick-dots li button {
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0;
  line-height: 0;
  width: 8px;
  height: 8px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}
.slick-dots li button::before {
  content: "";
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: white;
  border: 1px solid #122E63;
  border-radius: 50%;
  text-align: center;
  transition: all 0.2s ease-in-out;
}
.slick-dots li.slick-active button::before {
  background-color: #122E63;
}
.slick-dots li:only-child {
  display: none;
}

.composant {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: auto;
}
.composant .image {
  position: relative;
  border-radius: 20px;
  overflow: hidden;
  width: 100%;
  aspect-ratio: 1/1;
}
.composant .image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center center;
  background: linear-gradient(180deg, rgb(93, 108, 101) 34%, rgb(171, 160, 168) 78%);
  filter: saturate(120%) brightness(120%);
}
.composant .image .blue_cap {
  position: absolute;
  background-color: #122E63;
  color: #FFFFFF;
  bottom: 0;
  right: 0;
  min-width: 60%;
  text-align: center;
  border-top-left-radius: 20px;
  font-size: 11pt;
  padding: 2px 0;
}
.composant .text {
  display: flex;
  flex-direction: column;
  color: #122E63;
  justify-content: center;
  align-items: center;
}

.button {
  color: white;
  background-color: #C2B38F;
  border-radius: 50px;
  text-decoration: none;
  transition: all 0.2s ease-in-out;
  padding: 10px 40px;
  text-align: center;
  font-size: 20px;
}
.button:hover {
  background-color: #122E63;
}
.button.square {
  border-radius: 10px;
  box-shadow: inset 0 3px 6px rgba(0, 0, 0, 0.16);
  font-size: 22px;
}

main {
  display: flex;
  justify-content: center;
  background-color: #FFFFFF;
}
main .background-site {
  position: fixed;
  top: 0;
  object-fit: cover;
  width: 100%;
  z-index: 0;
}
main .sections_wrapper {
  max-width: 90%;
  width: 1380px;
  background-color: #FFFFFF;
  border-radius: 10px;
  padding: 7px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px;
  z-index: 1;
  margin: 80px auto;
}
@media screen and (min-width: 60rem) {
  main .sections_wrapper {
    padding: 70px;
  }
}

.sections_wrapper:has(.contact_block_solo) {
  display: flex;
}
.sections_wrapper:has(.contact_block_solo) .entry-content {
  margin: auto;
}
.sections_wrapper:has(.contact_block_solo) .entry-content p {
  max-width: 700px;
  margin: auto;
}
.sections_wrapper:has(.contact_block_solo) .entry-content p.has-text-align-center {
  text-align: center;
}
.sections_wrapper:has(.contact_block_solo) .entry-content .wp-block-columns {
  justify-content: space-between;
  margin: 20px 0;
}
.sections_wrapper:has(.contact_block_solo) .entry-content .wp-block-columns div h3, .sections_wrapper:has(.contact_block_solo) .entry-content .wp-block-columns div p {
  text-align: left;
}
.sections_wrapper:has(.contact_block_solo) .entry-content .wp-block-columns div h3 {
  margin: 10px 0;
}
.sections_wrapper:has(.contact_block_solo) .entry-content .wp-block-columns div:last-of-type h3, .sections_wrapper:has(.contact_block_solo) .entry-content .wp-block-columns div:last-of-type p {
  text-align: right;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

a:hover, a:active, a:focus {
  text-decoration: none;
}